import React from 'react';
import {
  Switch,
  Route,
} from 'react-router-dom';
import Home from '../pages/Home';
import WhatIsFrontend from '../pages/WhatIsFrontend';
import IDE from '../pages/IDE';
import HTML from '../pages/HTML';
import CSS from '../pages/СSS';
import JS from '../pages/JS';

const Routes = () => (
  <Switch>
    <Route path="/what-is-frontend">
      <WhatIsFrontend />
    </Route>

    <Route path="/ide">
      <IDE />
    </Route>

    <Route path="/html">
      <HTML />
    </Route>

    <Route path="/css">
      <CSS />
    </Route>

    <Route path="/js">
      <JS />
    </Route>

    <Route path="/">
      <Home />
    </Route>
  </Switch>
);

export default Routes;
