import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router';
import styles from './Navigation.module.scss';

const Navigation = (props) => {
  const { isHeader } = props;
  const { pathname } = useLocation();

  return (
    <nav className={classNames({ [styles.headerNavigation]: isHeader, [styles.homeNavigation]: !isHeader })}>
      <ul>
        <li className={styles.front}>
          <Link to="/what-is-frontend" className={classNames({ [styles.active]: pathname === '/what-is-frontend' })}>What is Front End?</Link>
        </li>
        <li className={styles.ide}>
          <Link to="/ide" className={classNames({ [styles.active]: pathname === '/ide' })}>IDE</Link>
        </li>
        <li className={styles.html}>
          <Link to="/html" className={classNames({ [styles.active]: pathname === '/html' })}>HTML</Link>
        </li>
        <li className={styles.css}>
          <Link to="/css" className={classNames({ [styles.active]: pathname === '/css' })}>CSS</Link>
        </li>
        <li className={styles.js}>
          <Link to="/js" className={classNames({ [styles.active]: pathname === '/js' })}>JS</Link>
        </li>
      </ul>
    </nav>
  );
};

Navigation.propTypes = {
  isHeader: PropTypes.bool,
};

Navigation.defaultProps = {
  isHeader: false,
};

export default Navigation;
