import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import styles from './FulImage.module.scss';
import { disableScroll, enableScroll } from '../../utils/scrollLock';

const FulImage = (props) => {
  const { src, alt, className } = props;
  const [fullWidth, setFullWidth] = useState(false);

  useEffect(() => {
    if (fullWidth) {
      disableScroll();
    } else {
      enableScroll();
    }
  }, [fullWidth]);

  return (
    <div
      className={classNames(styles.imgContainer, className, { [styles.fullWidth]: fullWidth })}
      onClick={() => setFullWidth((state) => !state)}
    >
      <img src={src} alt={alt} draggable={false} />
    </div>
  );
};

FulImage.propTypes = {
  src: PropTypes.string.isRequired,
  alt: PropTypes.string,
  className: PropTypes.string,
};

FulImage.defaultProps = {
  alt: '',
  className: '',
};

export default FulImage;
