import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { animateScroll, animateScroll as scroll } from 'react-scroll';
import { useLocation } from 'react-router';
import styles from './ScrollToTop.module.scss';

const ScrollToTop = () => {
  const { pathname } = useLocation();
  const [isShow, setIsShow] = useState(false);
  let isDebounce = false;

  const moveListener = () => {
    if (!isDebounce) {
      isDebounce = true;
      setTimeout(() => {
        isDebounce = false;
      }, 20);
      if (window.scrollY && window.scrollY > 150) {
        setIsShow(true);
      } else {
        setIsShow(false);
      }
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', moveListener, { passive: true });

    return () => {
      window.removeEventListener('scroll', moveListener);
    };
  }, []);

  useEffect(() => {
    window.scroll(0, 0);
  }, [pathname]);

  return (
    <button className={classNames(styles.root, { [styles.isShow]: isShow })} type="button" onClick={() => animateScroll.scrollToTop({ duration: 200 })}>
      <svg style={{ width: 24, height: 24 }} viewBox="0 0 24 24">
        <path fill="#fff" d="M12,22A10,10 0 0,1 2,12A10,10 0 0,1 12,2A10,10 0 0,1 22,12A10,10 0 0,1 12,22M12,7L7,12H10V16H14V12H17L12,7Z" />
      </svg>
    </button>
  );
};

ScrollToTop.propTypes = {};

ScrollToTop.defaultProps = {};

export default ScrollToTop;
