import React, { useState } from 'react';
import PropTypes from 'prop-types';
import styles from './HiddenResult.module.scss';

const HiddenResult = (props) => {
  const { children, dataForCopy } = props;
  const [hidden, setHidden] = useState(true);
  return (
    <div className={styles.root}>
      <button
        type="button"
        className={styles.button}
        onClick={() => setHidden((state) => !state)}
      >
        {`${hidden ? 'Подивитися' : 'Сховати'} результат`}
      </button>

      { !hidden && dataForCopy && (
      <button
        type="button"
        className={styles.button}
        onClick={() => {
          if (navigator && navigator.clipboard && navigator.clipboard.writeText) {
            navigator.clipboard.writeText(dataForCopy);
          }
        }}
      >
        Скопіювати
      </button>
      )}
      {!hidden && children}
    </div>
  );
};

HiddenResult.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
  dataForCopy: PropTypes.string,
};

HiddenResult.defaultProps = {
  dataForCopy: null,
};

export default HiddenResult;
