import React, { useEffect, useState, useMemo } from 'react';
import classNames from 'classnames';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router';
import styles from './Header.module.scss';
import Navigation from '../Navigation';

const Header = () => {
  const [isMoved, setIsMoved] = useState(false);
  const { pathname } = useLocation();
  let isDebounce = false;
  const moveListener = () => {
    if (!isDebounce) {
      isDebounce = true;
      setTimeout(() => {
        isDebounce = false;
      }, 10);
      if (window.scrollY && window.scrollY > 10) {
        setIsMoved(true);
      } else {
        setIsMoved(false);
      }
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', moveListener, { passive: true });

    return () => {
      window.removeEventListener('scroll', moveListener);
    };
  }, []);

  return useMemo(() => (
    <div className={classNames(styles.root, { [styles.moved]: isMoved })}>
      {pathname === '/'
        ? <img src="https://dan-it.com.ua/wp-content/themes/danIT/img/logo.png" alt="Logo" />
        : <Link to="/" className={styles.logoLink}><img src="https://dan-it.com.ua/wp-content/themes/danIT/img/logo.png" alt="Logo" /></Link>}
      <Navigation isHeader />
    </div>
  ), [isMoved, pathname]);
};

Header.propTypes = {};

Header.defaultProps = {};

export default Header;
