import React from 'react';
import classNames from 'classnames';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { materialLight } from 'react-syntax-highlighter/dist/esm/styles/prism';
import styles from './CSS.module.scss';
import cssImg from '../../assets/CSS/CSS3.png';
import selectorImg from '../../assets/CSS/selector.png';
import FulImage from '../../components/FulImage';
import HiddenResult from '../../components/HiddenResult/HiddenResult';
import { templateCSS, resolvedTemplateCSS } from '../../templates/css';

const CSS = () => (
  <section className={classNames('root', styles.root)}>
    <img className={styles.logo} src={cssImg} alt="CSS" height={150} />
    <h1>Cascading Style Sheets</h1>
    <hr />
    <p className="paragraph">CSS – це формальна мова, яка слугує для опису оформлення зовнішнього вигляду документа, створеного з використанням мови розмітки (HTML, XHTML, XML).</p>

    <p className="heading">Структура мови</p>
    <p className="paragraph">CSS можна охарактеризувати простими словами як набір правил, що описують, як має виглядати елемент.</p>
    <div className={styles.imgWrapper}>
      <FulImage src={selectorImg} alt="Selector" />
    </div>
    <a
      href="https://www.w3schools.com/cssref/css_selectors.asp"
      target="_blank"
      rel="noreferrer"
    >
      CSS селекторы
    </a>

    <p className="heading">Як підключити CSS</p>
    <p className="paragraph">CSS можна зв'язати з HTML кількома способами:</p>
    <ol>
      <li>Всередині тега за допомогою атрибуту style. При цьому немає потреби вказувати селектор.</li>
      <SyntaxHighlighter language="html" style={materialLight}>
        {`
<p style="font-size: 18px; font-weight: bold; color: plum;">Hello World</p>
            `}
      </SyntaxHighlighter>
      <li>Додати тег style с атрибутом type=&ldquo;text/css&ldquo;.</li>
      <SyntaxHighlighter language="html" style={materialLight}>
        {`     
<style>
    .container {
        padding: 10px 20px;
    }
            
    p {
        color: tomato;
    }
</style> `}
      </SyntaxHighlighter>
      <li>Підключити зовнішню таблицю стилів:</li>
      <SyntaxHighlighter language="html" style={materialLight}>
        {`
<link rel="stylesheet" href="путь до style.css" type="text/css" />
          `}
      </SyntaxHighlighter>
    </ol>

    <p className="heading">Завдання</p>
    <p className="paragraph">Опишіть стилі для проекту в окремому файлі *.css та підключіть їх до index.html </p>

    <SyntaxHighlighter language="css" style={materialLight}>
      {templateCSS}
    </SyntaxHighlighter>

    <HiddenResult dataForCopy={resolvedTemplateCSS}>
      <SyntaxHighlighter language="css" style={materialLight}>
        {resolvedTemplateCSS}
      </SyntaxHighlighter>
    </HiddenResult>
  </section>
);

export default CSS;
